.header {
  height: 45px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1.5 * $space;
  padding-right: 2 * $space;
  z-index: 9;
  // background-color: white;
  position: fixed;
  background: rgba(130, 130, 130, 0.5);
  backdrop-filter: blur(10px);
  // -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.17);
  // -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.17);
  // box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.17);
  
  &__logo-area {
    &_link {
      color: $blue;
      font-family: "Roboto";
      font-weight: 300;
      font-size: 24px;
    }
    &_img {
      width: $logo-desktop-width;
      height: auto;
    }
  }

  &__details-area {
    display: flex;

    &_searchbar {
      position: relative;
      input::-webkit-input-placeholder, input:-moz-placeholder {
        color: #fff !important;
      }
      &-gif {
        width: 30px;
        height: 30px;
        z-index: 1;
      }
    }

    &_items {
      display: flex;
      column-gap: 12px;
    
      &-icon {
        width: 32px;
        height: 32px;
        border-radius: 16px;
        cursor: pointer;
        background-color: #4382C4;
        display: flex;
        align-items: center;
        justify-content: center;
        
        img {
          width: 16px;
          height: 16px;
        }
      }
    }

    &_account {
      display: flex;
      margin-left: 20px;
    
      &-icon {
        width: 34px;
        height: 34px;
      }

      &-link {
        display: inline-block;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

